body{
  text-align: center;
  color: #000f08;
  margin: 0;
  font-family: 'Open Sans',sans-serif;
  width: 100%;
  }
  
  
  h1{
    font-weight: 800;
    font-size: 3.75em;
    margin-bottom: 1em;
  }
  
  h2{
    font-weight: 700;
    font-size: 2.5em;
  }

  h3{
    font-size: 1.75em;
  }

  h4{
    font-size: 1.75em;
  }
  
  h5 {
    font-size: 1.25em;
  }
  .subtitulo,h4,h5{
    font-weight: 400;
  }
  
  body * {
  margin: 0;
  border: 0;
  padding: 0;
  }
  
  .oscuro {
    background: #000f08;
    color: #ffffff;
  }
  
  .oscuro a{
    color: #ffffff;
    text-decoration: none;
  }
  
  .sweet-brown{
    text-align: left;
    background: #a4452c;
    color: #ffffff;
    padding: 50px;
    white-space: nowrap;
  }
  
  .hero-section{
    text-align: right;
    background-image: url('./img/bg-hero.png');
    background-repeat: no-repeat;
    background-size: cover; 
    color: #FFFFFF;
    text-shadow: 0.125em 0.25em 0.125em #707070;
    padding: 6.25em 3.125em;
  
  }
  
  section, footer{
    margin-top: 4.6875rem;
  }
  
  footer{
    padding-top: 3.125rem;
    padding-bottom: 3.125rem;
  
  }
  footer i{
    margin: 0 0.25em 0 0.25em;
  }
  
  .charla-texto, .evento{
    display: inline-block;
    vertical-align: middle;
  }
  
  .charla-speaker{
  width: 25%;
  vertical-align: middle;
  }
  .icono{
    width: 2rem;
  }
  
  .icono svg{
    fill: #ffffff;
  }
  
  .charla-texto{
  width: 25%;
  vertical-align: middle;
  margin-left: 5%;
  white-space: normal;
  
  }
  .centrado{
    width: 75%;
    margin: 0 auto;
    padding-left: 17.875rem;
  }
  
  nav{
    text-align: left;
    padding-top: 0.9564293305%;
    padding-bottom: 0.9564293305%;
    padding-left: 7.9375em;
    width: 112.0625rem;
    position: fixed;
  }
  
  nav ul {
    list-style-type: none;
  }
  
  nav ul li{
    display: inline-block;
    margin: 0 1em;
    vertical-align: middle;
  }
  
  .content {
    border: 0;
    margin: 0;
    padding-top: 12.25rem;
    padding-left: 26.125rem;
    padding-right: 26.125rem;
    width: 67.8125rem;
    text-align: left;
  }
  
  .content img{
  padding-top: 4.5625rem;
  padding-bottom: 4.5625rem;
  width: 100%;
  height: 35.3125rem;
  }
  
  .content p{
  
    padding-bottom: 2.125rem;
    width: 100%;
    font-size: 25px;
  
  }
  
  .segundo-titulo{
    padding-bottom: 3rem;
    padding-top: 0.9375rem;
  }
  
  article{
    padding-top: 4.6875rem;
  }
  
  article img{
  width: 25.9375rem;
  height: 15.25rem;
  padding-left: 4.53125rem;
  padding-right: 4.53125rem;
  }
  